import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ApprovedStocksComponent } from './approved-stocks.component';


const routes: Routes = [
  {
    path: '',
    component : ApprovedStocksComponent,
    data: {
      title: 'approvedstock'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ApprovedStocksRoutingModule { }
