import { Component, OnInit, ViewChild } from '@angular/core';
import { RequirementService } from '../../_services/requirement.service';
import { Offer } from '../../_models/Offer';
import { AlertifyService } from '../../_services/alertify.service';
import { Subject } from 'rxjs';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { DataTableDirective } from 'angular-datatables';

@Component({
  selector: 'app-approved-stocks',
  templateUrl: './approved-stocks.component.html',
  styleUrls: ['./approved-stocks.component.css']
})
export class ApprovedStocksComponent implements OnInit {
  model: any = {};
  pmodel: any = {};
  offers: Offer[];
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']]
};
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<Offer> = new Subject();
  roleid: any =  Number(localStorage.getItem('RoleID'));
  LoginUser: any =  localStorage.getItem('LoginUser');
  @ViewChild('poModal') public infoModal: ModalDirective;

  constructor(private alertify: AlertifyService, private requirementservice: RequirementService) { }

  ngOnInit(): void {
    this.loadDeals();
  }

  loadDeals() {
    return this.requirementservice.getOffers().subscribe((offers: Offer[]) => {
      console.log(offers);
      console.log(this.roleid);
      if (this.roleid === 4) {
        this.offers = offers.filter(proj => (proj.status !== 0 && proj.dispatchby
          && proj.empId === Number(localStorage.getItem('UserId'))));
      } else if (this.roleid === 7) {
        this.offers = offers.filter(proj => (proj.status !== 0 && !proj.dispatchby
          && proj.purchaseby !== null));
      } else if (this.roleid === 8) {
        this.offers = offers.filter(proj => (proj.status !== 0 && !proj.dispatchby
          && proj.receiveby !== null));
      } else {
        this.offers = offers.filter(proj => (proj.status !== 0 && !proj.dispatchby));
      }
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });
  }
  CloseDeals(Id, Status, flag, buttonid) {
    this.model = {Id : Id, Status: Status, Flag: flag, EmpId: Number(localStorage.getItem('UserId'))};
    document.getElementById(buttonid + '_' + Id).innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Processing...';
    (document.getElementById(buttonid + '_' + Id) as HTMLButtonElement).disabled = true;

    console.log(this.model);
    this.requirementservice.CloseDeals(this.model).subscribe(() => {
      this.loadDeals();
        this.alertify.success('Deal ' + buttonid);
    }, error => {
      this.alertify.error(error);
    });
  }
  OpenPO(id: any, req: any) {
    this.pmodel.id = id;
    this.pmodel.ReqNo1 = req;
    this.infoModal.show();
  }
  UpdatePOPost() {
    this.pmodel.po_by = Number(localStorage.getItem('UserId'));
    if (!this.pmodel.p_num) {
      this.alertify.error('Enter PO Number');
      return;
    }
    document.getElementById('POButton').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
   (document.getElementById('POButton') as HTMLButtonElement).disabled = true;
    this.requirementservice.UpdatePOPost(this.pmodel).subscribe(() => {
      document.getElementById('POButton').innerHTML = 'Submit';
     (document.getElementById('POButton') as HTMLButtonElement).disabled = false;
      this.loadDeals();
      this.infoModal.hide();
      this.alertify.success('PO Number Updated Successfully');
    }, error => {
      this.alertify.error(error);
    });
  }

}
